
    .coin-container {
      display: flex;
      justify-content: center;
      /* margin: 10px; */
    }

    .coin-row {
			overflow-x: scroll; 
      padding: 10px 0;
      width: 90%;
      justify-content: center;
      display: flex;
      flex-direction: row;
      align-items: center;
      border-bottom: 1px solid rgba(78, 228, 255, 0.438);
      margin: 2px;
    }

    .coin {
			overflow-x: scroll; 
      display: flex;
      align-items: center;
      padding-right: 4px;
      justify-content: space-between;
      width: 100%;
      /* background-color: aliceblue; */
    }

    .coin-name {
      display: flex;
      align-items: center;
      width: 25%;
      background: none;
      justify-content: left;
			padding-left: 5rem;
			font-weight: bold;
    }

    .coin img {
			
    min-width: 20px;
      width: 10%;
      height: auto;
      margin-right: 10px;
    }
    .coin-rank{
      width: 5%;
      background: none;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .coin-symbol {
      text-transform: uppercase;
      /* background-color: #374241; */
      width: 10%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .coin-price {
      width: 10%;
      background: none;
      text-align: right;
    }

    .coin-volume {
      width: 20%;
      background: none;
      text-align: right;
    }

    .coin-percent {
      width: 10%;
      background: none;
      text-align: right;
    }

    .coin-marketcap {
      width: 20%;
      background-color: none;
      text-align: right;
    }

    .red {
      color: rgb(255, 30, 0);
    }

    .green {
      color: rgb(36, 255, 7);
    }
