*{
  margin: 0; 
     padding: 0;
     box-sizing: border-box;
     font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }

		*::-webkit-scrollbar{
			display: none !important;
		}
		body{
			background: #222222;
			color: #fff
		}
	h2{
		margin-top: 2rem;
		text-align: center;
		font-size: 2rem;
		 font-family:Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		text-decoration: 2px red underline;
	}

    .form__group {
      position: relative;
      padding: 15px 0 0;
      margin:2rem auto;
      width: 50%;
    }

    .form__field {
      font-family: inherit;
      width: 100%;
      border: 0;
      border-bottom: 2px solid #9b9b9b;
      outline: 0;
      font-size: 1.3rem;
      color: #fff;
      padding: 7px 0;
      background: transparent;
      transition: border-color 0.2s;
    }

    .form__field::placeholder {
      color: transparent;
    }

    .form__field:placeholder-shown~.form__label {
      font-size: 1.3rem;
      cursor: text;
      top: 20px;
    }


    .form__label {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: #9b9b9b;
    }

    .form__field:focus~.form__label {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: #11998e;
      padding-bottom: 6px;
      font-weight: 700;
      border-width: 3px;
      border-image: linear-gradient(to right, #11998e, #38ef7d);
      border-image-slice: 1;
    }

    /* reset input */
    .form__field {
      box-shadow: none;
    }		
    .coin-head-row {
			overflow-x: scroll; 
      padding: 10px 0;
      width: 90%;
      justify-content: center;
      display: flex;
      flex-direction: row;
      align-items: center;
      /* border-top: 1px solid rgba(78, 228, 255, 0.438); */
      border-bottom: 1px solid rgba(78, 228, 255, 0.438);
      margin: 2px;
    }
    .coin-head {
      display: flex;
      align-items: center;
      padding-right: 4px;
      justify-content: space-between;
      width: 100%;
    }

    .coin-head-rank {
      width: 5% ;
      
      text-align: center;
    }
    .coin-head-name {
      width: 25%;
      justify-content: left;
			padding-left: 5rem;
    }

    .coin-head-symbol {
      width: 10%;
      text-align: center;
    }

    .coin-head-price {
      width: 10%;
      text-align: right;
    }

    .coin-head-volume {
      width: 20%;
      text-align: right;
    }

    .coin-head-percent {
      width: 10%;
      text-align: right;
    }

    .coin-head-marketcap {
      text-align: right;
      width: 20%;
    }
    .fork {
      width: 150px;
      height: 150px;
      position: fixed;
      top: -75px;
      right: -75px;
      transform: rotate(45deg);
      background: #ffffff;
      animation: bg-change 1.5s ease-in infinite;
    }

    @keyframes bg-change {

      0%,
      100% {
        background: #ffffff;
      }

      50% {
        background-color: #f0f0f0;
      }
    }

    .fork .logo {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      width: 100%;
      height: 100%;
      z-index: 1000;
    }

    .fork .logo a {
      width: 50px;
      height: 50px;
      color: #000000;
      z-index: 1000;
    }

    @media (max-width: 430px) {

      .fork {
        width: 120px;
        height: 120px;
        top: -60px;
        right: -60px;
        background: #11998e;
      }

      .fork .logo {
        width: 100%;
        height: 100%;
      }

      .fork .logo a {
        width: 40px;
        height: 40px;
      }
    }

    footer {
      padding: 20px;
      margin-top: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      border-top: 1px solid rgb(109, 109, 109);
      color: #cfcfcf;
      background-color: #292929;
    }

    footer ul {
      list-style: none;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    footer ul li {
      padding: 10px 5px;
    }

    footer ul a {
      padding: 5px;
      text-decoration: none;
      color: #cfcfcf;
      font-size: 1.3rem;
    }

    footer ul a:hover {
      text-decoration: underline 1px #fff;

      color: rgb(255, 255, 255);
    }